import React, { CSSProperties } from "react";

import "./index.scss";

interface VideoContainerState {
  childWidth: number | null;
}

const MIN_CHILD_WIDTH = 480;

export default class VideoContainer extends React.Component<{}, VideoContainerState> {
  private elem: HTMLDivElement | null = null;

  constructor(props: {}) {
    super(props);
    this.state = {
      childWidth: null,
    };
    this.getElemRef = this.getElemRef.bind(this);
  }

  public render() {
    const childStyle: CSSProperties = {};
    if (this.state.childWidth) {
      // take into account margins and borders
      childStyle.width = `${this.state.childWidth - 16}px`;
    }

    return (
      <div className="VideoContainer" ref={this.getElemRef}>
        {React.Children.map(this.props.children, child => (
          <div className="Video" style={childStyle}>{child}</div>
        ))}
      </div>
    )
  }

  public componentDidUpdate() {
    this.recalculateChildSizes();
  }

  private getElemRef(elem: HTMLDivElement) {
    this.elem = elem;
    this.recalculateChildSizes();
  }

  private recalculateChildSizes() {
    const desiredChildWidth = this.getDesiredChildWidth();
    if (desiredChildWidth !== this.state.childWidth) {
      this.setState({ childWidth: desiredChildWidth });
    }
  }

  private getDesiredChildWidth() {
    if (!this.elem) {
      return null;
    }

    const parentWidth = this.elem.getBoundingClientRect().width;
    let itemsPerRow = React.Children.count(this.props.children);
    let desiredSize;
    do {
      if (itemsPerRow === 0) {
        return MIN_CHILD_WIDTH;
      }
      desiredSize = parentWidth / itemsPerRow;
      --itemsPerRow;
    } while (desiredSize < MIN_CHILD_WIDTH);

    return desiredSize;
  }
}
